import React, { Suspense } from 'react';
import { Route, Router as VendorRouter, Switch } from 'react-router-dom';
import PropTypes from 'prop-types';
import { createTheme, StyledEngineProvider, ThemeProvider } from '@mui/material';

import { history, routes } from './routes';
import { ScrollToTop } from './scroll-to-top';
import { grey } from '@mui/material/colors';
import { Fallback } from '../components/Fallback';

const routeConfigTemplate = {
  name: PropTypes.string.isRequired,
  component: PropTypes.oneOfType([PropTypes.element, PropTypes.func]).isRequired,
};

const IPropTypes = {
  routeConfig: PropTypes.arrayOf(PropTypes.shape(routeConfigTemplate)).isRequired,
};

const zipRouteData = (routeData) => (
  routeData.map((data) => {
    const route = routes.find((r) => r.name === data.name);
    return {
      ...data,
      ...route,
    };
  })
);

const renderRoutes = (routeData) => (
  zipRouteData(routeData).map((data) => (
    <Route
      key={data.path}
      exact
      path={data.path}
      component={data.component}
    />
  ))
);

const theme = createTheme({
  typography: {
    fontFamily: 'Raleway',
    h1: {
      fontSize: '4rem',
      fontWeight: 600
    },
    body1: {
      color: grey[700],
      lineHeight: 1.66
    },
    body2: {
      color: grey[600],
      lineHeight: 1.5,
      fontSize: '1rem'
    },
    siteTitle: {
      fontSize: '1.4rem',
      fontWeight: 600,
      borderBottom: '1px solid',
      paddingBottom: '16px',
    },
    breadcrumb: {
      fontSize: '1rem',
      fontWeight: 600,
      color: grey[600],
      paddingBottom: '5px',
    },
    breadcrumbSelected: {
      color: '#1D1616',
      fontWeight: 600,
      fontSize: '1rem',
      borderBottom: '2px solid',
      paddingBottom: '5px',
    }
  },
  palette: {
    primary: {
      main: '#1D1616',
    },
  },
  components: {
    MuiTypography: {
      defaultProps: {
        variantMapping: {
          siteTitle: 'h4'
        }
      }
    },
    MuiButton: {
      styleOverrides: {
        root: {
          paddingTop: '10px',
          paddingBottom: '10px',
          textTransform: 'none',
        },
        containedSizeLarge: {
          paddingTop: '12px',
          paddingBottom: '12px'
        }
      }
    }
  }
});

theme.typography.h1 = {
  fontSize: '2rem',
  [theme.breakpoints.up('md')]: {
    fontSize: '3rem',
  },
  [theme.breakpoints.up('lg')]: {
    fontSize: '4rem',
  }
}

const Router = (props) => {
  const { routeConfig } = props;
  return (
    <VendorRouter history={history}>
      <ScrollToTop>
        <StyledEngineProvider injectFirst>
          <Suspense fallback={<Fallback />}>
            <Switch>
              <ThemeProvider theme={theme}>
                {renderRoutes(routeConfig)}
              </ThemeProvider>
            </Switch>
          </Suspense>
        </StyledEngineProvider>
      </ScrollToTop>
    </VendorRouter>
  );
};

Router.propTypes = IPropTypes;

export { Router };
